$project: "vrembem";
$description: "A BEM powered CSS component library.";
$version: "0.1.3";
$author: "Sebastian Nitu";
$repo: "https://github.com/sebnitu/vrembem.git";
$url: "http://sebnitu.com";
$year: 2017;
$license: "MIT";
@import 'vrem-core';
@include add-comment('html');

html {
  margin: 0;
  padding: 0;
  box-sizing: $box-sizing;
  font-size: $font-size;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
